require("stylesheets/components/buttons/indicatorButton.scss");

var IconButton = require("./iconButton.js").IconButton;

class IndicatorButton extends IconButton{
  constructor(className, indicatorClass){
    super(className);
    this.indicatorClass = indicatorClass;

    this._indicator = false;
  }

  get indicator(){
    return this._indicator;
  }

  set indicator(indicator){
    this._indicator = indicator;
    this.domNode.classList.toggle(this.indicatorClass, indicator);

    return this._indicator;
  }
}

var toExport = {
  IndicatorButton
};

if(typeof module !== "undefined" && typeof module.exports !== "undefined"){
  module.exports = toExport;
} else {
  Object.assign(window, toExport);
}



